/*--------------------------------------------------------------
# Base
--------------------------------------------------------------*/
/* Typography
--------------------------------------------- */
body,
button,
input,
select,
optgroup,
textarea {
  color: #454545;
  font-family: "VerdanaPro", sans-serif;
  font-size: 1rem;
  line-height: 1.4;
}

button {
  color: #fff;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: bold;
  clear: both;
}

h1 {
  margin: 0;
}

p {
  margin-bottom: 1.5em;
}

dfn,
cite,
em,
i {
  font-style: italic;
}

blockquote {
  margin: 0 1.5em;
}

address {
  margin: 0 0 1.5em;
}

pre {
  background: #eee;
  font-family: "Courier 10 Pitch", courier, monospace;
  line-height: 1.4;
  margin-bottom: 1.6em;
  max-width: 100%;
  overflow: auto;
  padding: 1.6em;
}

code,
kbd,
tt,
var {
  font-family: monaco, consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
}

abbr,
acronym {
  border-bottom: 1px dotted #666;
  cursor: help;
}

mark,
ins {
  background: #fff9c0;
  text-decoration: none;
}

big {
  font-size: 125%;
}

/* Elements
--------------------------------------------- */
:root {
  color-primary: #4c8dc4;
  color-primary-light: #72a5d1;
  color-primary-dark: #3773a6;
  color-secondary: #fa4b21;
  color-secondary-light: #fb7353;
  color-secondary-dark: #e33005;
}

body {
  background: #f2f2f2;
}

hr {
  background-color: #ccc;
  border: 0;
  height: 1px;
  margin-bottom: 1.5em;
}

ul,
ol {
  margin: 0 0 1.5em 0;
}

@media (min-width: 1200px) {
  ul,
ol {
    margin: 0 0 1.5em 0.5em;
    padding-left: 1.5em;
  }
}
ul {
  list-style: disc;
}

ol {
  list-style: decimal;
}

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: 1.5em;
}

dt {
  font-weight: 700;
}

dd {
  margin: 0 1.5em 1.5em;
}

.f-list {
  list-style: none;
  margin: 0;
  padding-left: 25px;
}
.f-list li {
  position: relative;
  padding-left: 1.5em;
  font-size: 20px;
  margin-bottom: 0.7em;
}
.f-list li:before {
  position: absolute;
  font-family: "Font Awesome 6 Pro";
  font-weight: bold;
  font-size: 0.9em;
  color: #aeaabb;
  top: 2px;
  left: 0px;
  content: "";
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
  max-width: 100%;
}

img {
  height: auto;
  max-width: 100%;
}

figure {
  margin: 1em 0;
}

.table-wrap {
  overflow: auto;
}

table {
  margin: 0 0 1.5em;
  text-align: left;
  border-collapse: collapse;
}
@media (min-width: 500px) {
  table {
    width: 100%;
  }
}
table tbody tr:nth-of-type(even),
table tfoot tr:nth-of-type(even) {
  background-color: rgba(76, 141, 196, 0.05);
}
table tbody tr:hover,
table tfoot tr:hover {
  background-color: rgba(114, 165, 209, 0.2);
}
table tbody tr:first-of-type:hover,
table tfoot tr:first-of-type:hover {
  background-color: unset;
}
table td,
table th {
  padding: 0.5em;
  color: #454545;
  border: 1px solid #72a5d1;
}
table thead {
  background-color: #4c8dc4;
}
table thead th,
table thead td {
  color: #fff;
  font-weight: normal;
  border: 1px solid #72a5d1;
}
table p {
  margin: 0;
}

/* Links
--------------------------------------------- */
a {
  color: #454545;
  text-decoration: none;
}

.woocommerce-breadcrumb a,
.link-dark {
  color: #454545;
  text-decoration: none;
  transition: border-color 0.3s;
  border-bottom: 1px solid rgba(55, 115, 166, 0.2);
}
.woocommerce-breadcrumb a:visited, .woocommerce-breadcrumb a:hover, .woocommerce-breadcrumb a:focus, .woocommerce-breadcrumb a:active,
.link-dark:visited,
.link-dark:hover,
.link-dark:focus,
.link-dark:active {
  color: #454545;
  border-bottom: 1px solid rgba(55, 115, 166, 0.5);
  outline: 0;
}

.link-light {
  color: #fff;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  transition: border-color 0.3s;
}
.link-light:hover, .link-light:focus, .link-light:active {
  color: #fff;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}

a.post-thumbnail {
  border-bottom: none;
}

figure a {
  border-bottom: unset !important;
}

/* Forms
--------------------------------------------- */
.btn,
.button,
.added_to_cart,
.wc-block-product-search__button,
input[type=button],
input[type=reset],
input[type=submit] {
  display: inline-block;
  padding: 0.8em 1.6em;
  min-width: 186px;
  border: 1px solid;
  border-radius: 5px;
  border-color: #fa4b21;
  background: #fa4b21;
  color: white;
  font-size: 0.9em;
  font-weight: bold;
  text-align: center;
  line-height: 1;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s;
}
.btn:visited, .btn:focus,
.button:visited,
.button:focus,
.added_to_cart:visited,
.added_to_cart:focus,
.wc-block-product-search__button:visited,
.wc-block-product-search__button:focus,
input[type=button]:visited,
input[type=button]:focus,
input[type=reset]:visited,
input[type=reset]:focus,
input[type=submit]:visited,
input[type=submit]:focus {
  color: white;
  border-color: #fa4b21;
  background: #fa4b21;
}
.btn:active, .btn:hover,
.button:active,
.button:hover,
.added_to_cart:active,
.added_to_cart:hover,
.wc-block-product-search__button:active,
.wc-block-product-search__button:hover,
input[type=button]:active,
input[type=button]:hover,
input[type=reset]:active,
input[type=reset]:hover,
input[type=submit]:active,
input[type=submit]:hover {
  border-color: #fb7353;
  color: #fff;
  background: #fb7353;
}

.btn-altair {
  color: #4c8dc4;
  font-weight: bold;
  font-size: 0.9em;
  border-bottom: none !important;
  transition: all 0.3s;
}
.btn-altair:active, .btn-altair:hover {
  color: #3773a6;
}

.btn-invert {
  border-color: #fa4b21;
  background: transparent;
  color: #fa4b21;
}
.btn-invert:visited, .btn-invert:focus {
  color: #fff;
  border-color: #fa4b21;
  background: #fa4b21;
}
.btn-invert:active, .btn-invert:hover {
  border-color: #fa4b21;
  color: #fff;
  background: #fa4b21;
}

.added_to_cart {
  margin-top: 0.5em;
  width: 100%;
}

.wc-block-product-search__button {
  min-width: unset;
}

input[type=text],
input[type=email],
input[type=url],
input[type=password],
input[type=search],
input[type=number],
input[type=tel],
input[type=range],
input[type=date],
input[type=month],
input[type=week],
input[type=time],
input[type=datetime],
input[type=datetime-local],
input[type=color],
textarea {
  color: #666;
  border: 1px solid #ccc;
}
input[type=text]:focus,
input[type=email]:focus,
input[type=url]:focus,
input[type=password]:focus,
input[type=search]:focus,
input[type=number]:focus,
input[type=tel]:focus,
input[type=range]:focus,
input[type=date]:focus,
input[type=month]:focus,
input[type=week]:focus,
input[type=time]:focus,
input[type=datetime]:focus,
input[type=datetime-local]:focus,
input[type=color]:focus,
textarea:focus {
  color: #111;
}

select {
  border: 1px solid #ccc;
}

textarea {
  width: 100%;
}

