/*--------------------------------------------------------------
# Layouts
--------------------------------------------------------------*/
.container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}
@media (max-width: 1200px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.site {
  display: flex;
  flex-direction: column-reverse;
}
@media (min-width: 1100px) {
  .site {
    margin-bottom: 25px;
    display: grid;
    grid-gap: 25px;
    grid-template-columns: 25% auto;
    grid-template-areas: "sidebar main";
  }
}

.site-header {
  grid-area: header;
}

.site-main {
  grid-area: main;
  padding-top: 0;
  padding-bottom: 1em;
}
.home .site-main {
  padding: 0 1em 0 1em;
}

